import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const store = new Vuex.Store({
  namespaced: true,
  state: {
    currentComponent: 'About',
    oldComponent: null
  },
  mutations: {
    set (state, component) {
      state.oldComponent = state.currentComponent === component ? null : state.currentComponent
      state.currentComponent = component
    }
  },
  actions: {
    setView: (curView) => {
      store.commit('set', curView)
    }
  }
})

export default store;