        },
        {
          name: `Noteful`,
          desc: `Noteful is a sheet music learning app using spaced repitition that will
            get you up to speed quickly.`,
          icon: require('../../assets/work_icons/noteful.png'),
          videoSrc: require('../../assets/webms/Noteful.webm'),
          tech: [
            require('../../assets/tech_icons/react.png'),
            require('../../assets/tech_icons/redux.png'),
            require('../../assets/tech_icons/node.png'),
            require('../../assets/tech_icons/mongodb.png'),
            require('../../assets/tech_icons/js.png'),
            require('../../assets/tech_icons/html5.png'),
