          link: 'https://github.com/thinkful-ei24/Noteful-Client',
          repoIcon: require('../../assets/tech_icons/github.png'),
          bgColor: 'linear-gradient(-20deg, #FFC318 0%, #E73C00 100%)'
        }
      ]
    }
  },
  methods: {
    showSlides: function(n) {
      let i;
      let slides = document.getElementsByClassName('mySlides')
      let dots = document.getElementsByClassName('dot')
      if (n > slides.length) { this.slideIndex = 1 }
      if (n < 1) { this.slideIndex = slides.length }
      for (i = 0; i < slides.length; i++) {
        slides[i].style.display = 'none'
      }
      for (i = 0; i < dots.length;  i++) {
        dots[i].className = dots[i].className.replace(' active', '');
      }
      slides[this.slideIndex - 1].style.display = 'block'
      dots[this.slideIndex - 1].className += ' active'
    },
    plusSlides: function(n) {
      this.showSlides(this.slideIndex += n)
    },
    currentSlide: function(n) {
      // console.log(this.$refs.videoplayer)
      // this.$refs.videoplayer.src = this.works[this.slideIndex-1].videoSrc

      this.showSlides(this.slideIndex = n)
    },
    changeColor: function(val) {
      this.bgColor = val
    },
  },
}
</script>

